import { Player } from '../types/court';
import { v4 as uuidv4 } from 'uuid';

// 
export const generateTestPlayers = (): Player[] => {
    const names = [
        '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '',
        'John', 'Mary', 'Tom', 'Jerry', 'Mike', 'Sarah', 'David', 'Linda'
    ];

    return names.map(name => ({
        id: uuidv4(),
        name,
        isPlaying: false,
        isQueuing: false
    }));
}; 